import { Component, EventEmitter, Input, Output } from '@angular/core';
import { NgbDropdown } from '@ng-bootstrap/ng-bootstrap';
import { PbdsColumnToggle } from './column-toggle.interfaces';

@Component({
  selector: 'pbds-column-toggle',
  templateUrl: './column-toggle.component.html',
  styleUrls: []
})
export class PbdsColumnToggleComponent {
  @Input()
  public columns: any[];

  @Input()
  public storagekey: string | false = false;

  @Input()
  public minimum: number = 1;

  @Output()
  public toggle: EventEmitter<PbdsColumnToggle> = new EventEmitter();

  public columnStorage: any;
  private totalSelected: number;

  public ngOnInit() {
    if (this.storagekey) {
      this.columnStorage = localStorage.getItem(this.storagekey);

      if (this.columnStorage) {
        this.columnStorage = JSON.parse(this.columnStorage);

        this.columns.map((column) => {
          const savedColumn = this.columnStorage.find((obj: any) => obj.field === column.field);
          column.toggle.selected = savedColumn ? savedColumn.toggle.selected : true;
        });
      }

      this.setLocalStorage();
    }

    this.updateTotalSelected();
  }

  public toggleColumn(column: any) {
    // prevent unchecking all columns
    if (this.totalSelected === this.minimum && column.toggle.selected) {
      return;
    }

    column.toggle.selected = !column.toggle.selected;

    if (this.storagekey) {
      this.setLocalStorage();
    }

    this.toggle.emit({
      showAll: false,
      column,
      columns: this.columns
    });

    this.updateTotalSelected();
  }

  public showAllColumns(columnToggleDropdown: NgbDropdown) {
    this.columns.map((column) => {
      if (column.toggle.visible) {
        column.toggle.selected = true;
      }
    });

    if (this.storagekey) {
      this.setLocalStorage();
    }

    this.toggle.emit({
      showAll: true,
      column: null,
      columns: this.columns
    });

    this.updateTotalSelected();
    columnToggleDropdown.close();
  }

  public showSelectedIcon(column: any) {
    return column.toggle.selected ? '' : 'invisible';
  }

  private setLocalStorage() {
    if (this.storagekey) {
      localStorage.setItem(this.storagekey, JSON.stringify(this.columns));
    }
  }

  private updateTotalSelected() {
    this.totalSelected = this.columns.filter((value) => value.toggle.selected && value.toggle.visible).length;
  }
}
